.landing-page-container {
	display: flex;
	background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),
		url("../images/spacex.jpg");
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	flex-direction: column;
	height: 100vh;
}

.landing-page-container .content {
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	align-items: center;
	width: 80%;
	margin: 0 auto;
}

.content h1 {
	color: whitesmoke;
	font-size: 2.8rem;
}

.content p {
	color: whitesmoke;
	padding: 1rem;
	line-height: 2rem;
	letter-spacing: 0.1rem;
	font-size: 1.1rem;
}

.content .launches-btn {
	position: relative;
	display: block;
	width: 10rem;
	margin: 1.3rem 1rem;
	padding: 0.8rem;
	font-weight: 700;
	text-decoration: none !important;
	font-size: 0.9rem;
	letter-spacing: 1.2px;
	color: whitesmoke;
	background: transparent;
	border: 2px #2242d4 solid;
	border-radius: 6px;
	text-transform: uppercase;
	outline: 0;
	overflow: hidden;
	cursor: pointer;
	transition: all 0.3s ease-in-out 0s;
	-o-transition: all 0.3s ease-in-out 0s;
	-ms-transition: all 0.3s ease-in-out 0s;
	-moz-transition: all 0.3s ease-in-out 0s;
	-webkit-transition: all 0.3s ease-in-out 0s;
}

.content .launches-btn:hover {
	background: #264280f6;
	border: 2px black solid;
	color: whitesmoke;
}

/* // */

@media screen and (max-width: 770px) {
	.content h1 {
		font-size: 2.2rem;
		margin-bottom: 1.5rem;
	}

	.landing-page-container .content {
		height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: left;
		align-items: center;
		width: 80%;
		margin: 0 auto;
	}
	.content p {
		padding: 0.5rem;
		font-size: 1rem;
		line-height: 1.7rem;
		letter-spacing: 0rem;
	}

	.content .launches-btn {
		width: 40vw;
		text-align: center;
		margin: 1rem 1rem;
		padding: 0.7rem;
		font-weight: 500;
		font-size: 0.8rem;
		letter-spacing: 1.1px;
	}
}
