.App {
	background-image: url(/static/media/spacex.ce94a25b.jpg);
	background-position: center;
	background-repeat: no-repeat; /* Do not repeat the image */
	background-size: cover;
	width: "100vw";
}

.icon {
	padding: 10px;
	height: "40px";
	width: "40px";
	cursor: pointer;
}

@media screen and (max-width: 500px) {
	.icon {
		padding: 5px;
		height: "20px";
		width: "20px";
		cursor: pointer;
	}
}

.header-container {
	position: fixed !important;
	top: 0;
	z-index: 9999999;
	display: flex;
	background: black;
	flex-direction: row;
	padding: 1.2rem;
	align-items: center;
	box-shadow: 2px 2px 4px rgb(143, 179, 209);
	justify-content: space-between;
	width: 100%;
	height: 9vh;
}

.navLink {
	text-decoration: none;
	margin-right: 19px;
	color: white;
	font-weight: 700;
	font-size: large;
}

.navbar-collapse {
	background-color: black !important;
	width: 100vw !important;
}

.navbar-nav {
	margin-left: 955px;
}

.header-container .logo {
	font-size: 1.6rem;
	cursor: pointer;
	font-family: "Do Hyeon", sans-serif;
}

.logo img {
	height: 4.5rem;
	width: 9rem;
	margin-right: 0.5rem;
}

.anchor {
	margin: 1rem;
	color: rgba(224, 210, 210, 0.87) !important;
	cursor: pointer;
}

.active {
	color: white !important;
}

@media screen and (max-width: 411px) {
	/* Navbar */
	.header-container {
		padding: 1rem;
		height: 9vh;
	}

	.navbar-nav {
		width: 100% !important;
		display: flex !important;
		justify-content: center !important;
		align-items: center !important;
	}

	.header-container .logo {
		font-size: 1.3rem;
	}

	.logo img {
		height: 2.4rem;
		width: 2.4rem;
	}

	.anchor {
		font-size: 0.8rem;
	}
	.navbar-nav {
		margin-left: 65px;
	}
}

@media screen and (max-width: 320px) {
	.header-container {
		flex-direction: column;
	}
	.logo img {
		height: 1.6rem;
		width: 1.6rem;
		margin-right: 0.5rem;
		padding-right: 0rem !important;
	}
	.navbar-brand {
		display: block !important;
		padding-right: 0rem !important;
		margin-right: 0rem !important;
	}
	.navbar-nav {
		margin-left: 75px;
	}
}
@media screen and (max-width: 380px) {
	.navbar-nav {
		margin-left: 30px;
	}
}

.landing-page-container {
	display: flex;
	background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),
		url(/static/media/spacex.ce94a25b.jpg);
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	flex-direction: column;
	height: 100vh;
}

.landing-page-container .content {
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	align-items: center;
	width: 80%;
	margin: 0 auto;
}

.content h1 {
	color: whitesmoke;
	font-size: 2.8rem;
}

.content p {
	color: whitesmoke;
	padding: 1rem;
	line-height: 2rem;
	letter-spacing: 0.1rem;
	font-size: 1.1rem;
}

.content .launches-btn {
	position: relative;
	display: block;
	width: 10rem;
	margin: 1.3rem 1rem;
	padding: 0.8rem;
	font-weight: 700;
	text-decoration: none !important;
	font-size: 0.9rem;
	letter-spacing: 1.2px;
	color: whitesmoke;
	background: transparent;
	border: 2px #2242d4 solid;
	border-radius: 6px;
	text-transform: uppercase;
	outline: 0;
	overflow: hidden;
	cursor: pointer;
	transition: all 0.3s ease-in-out 0s;
	-o-transition: all 0.3s ease-in-out 0s;
	-ms-transition: all 0.3s ease-in-out 0s;
	-moz-transition: all 0.3s ease-in-out 0s;
	-webkit-transition: all 0.3s ease-in-out 0s;
}

.content .launches-btn:hover {
	background: #264280f6;
	border: 2px black solid;
	color: whitesmoke;
}

/* // */

@media screen and (max-width: 770px) {
	.content h1 {
		font-size: 2.2rem;
		margin-bottom: 1.5rem;
	}

	.landing-page-container .content {
		height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: left;
		align-items: center;
		width: 80%;
		margin: 0 auto;
	}
	.content p {
		padding: 0.5rem;
		font-size: 1rem;
		line-height: 1.7rem;
		letter-spacing: 0rem;
	}

	.content .launches-btn {
		width: 40vw;
		text-align: center;
		margin: 1rem 1rem;
		padding: 0.7rem;
		font-weight: 500;
		font-size: 0.8rem;
		letter-spacing: 1.1px;
	}
}

